import React, { useState, useEffect, useRef } from 'react';
import { Button } from 'antd';
import { useNavigate, useLocation } from 'react-router-dom';
import './Dialog.scss';
import soundEffect from '../../assets/alert.mp3'; // 

const Dialog = (props) => {
  const {
    visible,                  // 
    onClose,                  // 
    dialogData = [],          // 
    currentIndex = 0,         // 
    onPrevious,               // 
    setCurrentDialogIndex,    // 
    dialogs = [],             // 
    savePageState,            // 
  } = props;

  const [isPlaying, setIsPlaying] = useState(false); // 
  const audioRef = useRef(new Audio(soundEffect));   // 
  const navigate = useNavigate();                    // 
  const location = useLocation();                    // 

  const { title, content, imageUrl } = dialogData[currentIndex] || {}; // 

  // 
  const updateBackgroundClass = (index) => {
    let newClass = 'background-image-01';
    if (index >= 19 && index <= 26) {
      newClass = 'background-image-02';
    } else if (index >= 27 && index <= 38) {
      newClass = 'background-image-03';
    } else if (index >= 39 && index <= 41) {
      newClass = 'background-image-04';
    } else if (index > 41) {
      newClass = 'background-image-05';
    }
    document.body.className = newClass;
  };

  // 
  const handlePreviousDialog = () => {
    if (currentIndex > 0) {
      onPrevious();
    }
  };

  // 
  const handleNextDialog = () => {
    setCurrentDialogIndex((prevIndex) => {
      if (prevIndex < dialogs.length - 1) {
        const newIndex = prevIndex + 1;
        updateBackgroundClass(newIndex); // 
        savePageState();
        return newIndex;
      }
      return prevIndex;
    });
  };

  // 
  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const dialogIndex = queryParams.get('dialogIndex');
    if (dialogIndex !== null) {
      setCurrentDialogIndex(parseInt(dialogIndex, 10));
    }
  }, [location.search, setCurrentDialogIndex]);

  // 
  useEffect(() => {
    const audio = audioRef.current;

    // 1820
    const isBetween18And20 = currentIndex >= 18 && currentIndex <= 20;

    if (isBetween18And20) {
      // 1820
      audio.loop = true;
      if (!isPlaying) {
        audio.play();
        setIsPlaying(true);
      }
    } else {
      // 1820
      audio.pause();
      audio.currentTime = 0;
      setIsPlaying(false);
    }

    return () => {
      // 
      audio.pause();
      audio.currentTime = 0;
      setIsPlaying(false);
    };
  }, [currentIndex]);

  // 
  const buttonConfigs = {
    4: { text: "", onClick: () => navigate('/Game') },
    8: { text: "", onClick: () => navigate('/Game2') },
    20: { text: "", onClick: () => navigate('/Game3') },
    37: { text: "", onClick: () => navigate('/Game4') },
    40: { text: "", onClick: () => navigate('/Game5') },
    46: { text: "", onClick: () => navigate('/Game6') },
    66: { text: "", onClick: () => navigate('/Game7') },
    80: { text: "", onClick: () => navigate('/Game8') },
    94: { text: "", onClick: () => navigate('#') },
  };

  return (
    <>
      {/*  */}
      {imageUrl && <img src={imageUrl} alt="Image" className="custom-modal-image-font" />}
      {visible && (
        // 
        <div className="custom-modal-overlay" onClick={(e) => e.stopPropagation()}></div>
      )}
      <div className="custom-modal">
        <div>
          {/*  */}
          {imageUrl && <img src={imageUrl} alt="Image" className="custom-modal-image" />}
          {/*  */}
          <span className="custom-modal-title">{title}</span>
        </div>
        {/*  */}
        <div className="custom-modal-content">{content}</div>
        <Button
          className="custom-modal-button"
          onClick={handlePreviousDialog}
          disabled={currentIndex === 0}
        >
          
        </Button>
        {!Object.keys(buttonConfigs).includes(String(currentIndex)) && (
          <Button
            className="custom-modal-button"
            onClick={handleNextDialog}
          >
            
          </Button>
        )}
      </div>
      {buttonConfigs[currentIndex] && (
        <Button
          className="assemble-button"
          onClick={buttonConfigs[currentIndex].onClick}
        >
          {buttonConfigs[currentIndex].text}
        </Button>
      )}
    </>
  );
};

export default Dialog;
