import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import assemblyImage from '../../assets/1-.png'; // 
import './Game2.scss';
import background from '../../assets/picture/BG/-.png'; // 

const Game = () => {
  const [isAssembling, setIsAssembling] = useState(false); // 
  const navigate = useNavigate();

  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 9 } });
  };

  const handleStartAssembling = () => {
    setIsAssembling(true); // 
  };

  return (
    <div
      className="container"
      style={{
        backgroundImage: `url(${background})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        height: '100vh',
        width: '100vw',
      }}
    >
      {!isAssembling ? (
        <div className="content">
          <img src={assemblyImage} alt="" className="image" />
          <button className="button" onClick={handleStartAssembling}></button>
        </div>
      ) : (
        <div className="content">
          <img src={assemblyImage} alt="" className="image" />
          <button className="button" onClick={handleEndGame}></button>
        </div>
      )}
    </div>
  );
};

export default Game;
