import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import mapImage from '../../assets/1-.png'; // 
import audio1 from '../../assets/alert.mp3'; // 1
import audio2 from '../../assets/alert.mp3'; // 2
import audio3 from '../../assets/alert.mp3'; // 3
import './Game4.scss'; //  SCSS 

const Game = () => {
  const [audioPlayed, setAudioPlayed] = useState([false, false, false]); // 
  const [inputValue, setInputValue] = useState(''); // 
  const [showEndButton, setShowEndButton] = useState(false); // 
  const navigate = useNavigate();

  // 
  const handlePlayAudio = (index) => {
    const audioFiles = [audio1, audio2, audio3]; // 
    const audio = new Audio(audioFiles[index]);
    audio.play();
    setAudioPlayed(audioPlayed.map((played, i) => (i === index ? true : played)));

    // 
    if (index === 1) {
      checkInputValue();
    }
  };

  // 
  const checkInputValue = () => {
    if (inputValue === '') {
      setShowEndButton(true); // 
    }
  };

  // 
  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 38 } });
  };

  // 
  const clearResultAndAudio = () => {
    setInputValue(''); // 
    setAudioPlayed([false, false, false]);
    setShowEndButton(false); // 
  };

  return (
    <div className="container">
      <div className="mapContainer">
        {/*  */}
        <img src={mapImage} alt="" className="mapImage" />
      </div>
      <div className="buttonsContainer">
        {/*  */}
        <button
          className={`button ${audioPlayed[0] ? 'audioPlayed' : ''}`}
          onClick={() => handlePlayAudio(0)}
          disabled={audioPlayed[0]}
        >
           1
        </button>
        <button
          className={`button ${audioPlayed[1] ? 'audioPlayed' : ''}`}
          onClick={() => handlePlayAudio(1)}
          disabled={audioPlayed[1]}
        >
           2
        </button>
        <button
          className={`button ${audioPlayed[2] ? 'audioPlayed' : ''}`}
          onClick={() => handlePlayAudio(2)}
          disabled={audioPlayed[2]}
        >
           3
        </button>
      </div>
      <div className="resultContainer">
        {/*  */}
        <input
          type="text"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          onBlur={checkInputValue} // 
          placeholder=""
          className="input"
        />
        {showEndButton && (
          <div>
            {/*  */}
            <button className="button" onClick={handleEndGame}>
              
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default Game;
